import { getDayWiseDataG } from "./geoJson";

/**
 * Calculate cumulative trek statistics across all days
 * @returns {Object} Trek statistics including totals and pass information
 */
export const calculateTrekStats = () => {
  const routes = getDayWiseDataG();
  const days = Object.keys(routes).map(Number).sort((a, b) => a - b);
  
  let totalDistance = 0; // in km
  let totalClimb = 0; // in feet
  let totalDescent = 0; // in feet
  let activeDays = 0;
  let restDays = 0;
  let maxAltitude = 0;
  let minAltitude = Infinity;
  
  const passeDays = [];
  
  days.forEach(day => {
    const route = routes[day];
    const props = route.features[0]?.properties;
    
    if (!props) return;
    
    // Parse distance (format: "4.66 mi / 7.5 km")
    if (props.distance && props.distance !== "0 mi / 0 km") {
      const kmMatch = props.distance.match(/([\d.]+)\s*km/);
      if (kmMatch) {
        totalDistance += parseFloat(kmMatch[1]);
      }
    }
    
    // Parse elevation gain
    if (props.total_climb) {
      const climb = parseInt(props.total_climb.replace(/,/g, ''));
      if (!isNaN(climb)) totalClimb += climb;
    }
    
    // Parse descent
    if (props.descent) {
      const desc = parseInt(props.descent.replace(/,/g, ''));
      if (!isNaN(desc)) totalDescent += desc;
    }
    
    // Count day types
    if (props.time === "Rest Day") {
      restDays++;
    } else {
      activeDays++;
    }
    
    // Track max/min altitude
    if (props.peakAlt) {
      const peak = parseInt(props.peakAlt.replace(/,/g, ''));
      if (!isNaN(peak)) maxAltitude = Math.max(maxAltitude, peak);
    }
    if (props.startAlt) {
      const start = parseInt(props.startAlt.replace(/,/g, ''));
      if (!isNaN(start)) {
        maxAltitude = Math.max(maxAltitude, start);
        minAltitude = Math.min(minAltitude, start);
      }
    }
    if (props.endAlt) {
      const end = parseInt(props.endAlt.replace(/,/g, ''));
      if (!isNaN(end)) {
        maxAltitude = Math.max(maxAltitude, end);
        minAltitude = Math.min(minAltitude, end);
      }
    }
    
    // Identify pass days (high altitude crossings with significant elevation)
    if (props.peakAlt) {
      const peak = parseInt(props.peakAlt.replace(/,/g, ''));
      if (!isNaN(peak) && peak >= 17500) { // Major passes above 17,500ft
        passeDays.push({
          day,
          name: props.name,
          altitude: peak
        });
      }
    }
  });
  
  return {
    totalDistance: Math.round(totalDistance * 10) / 10, // km
    totalDistanceMi: Math.round(totalDistance * 0.621371 * 10) / 10, // miles
    totalClimb, // feet
    totalClimbM: Math.round(totalClimb * 0.3048), // meters
    totalDescent, // feet
    totalDescentM: Math.round(totalDescent * 0.3048), // meters
    activeDays,
    restDays,
    totalDays: days.length,
    maxAltitude, // feet
    maxAltitudeM: Math.round(maxAltitude * 0.3048), // meters
    minAltitude: minAltitude === Infinity ? 0 : minAltitude, // feet
    minAltitudeM: minAltitude === Infinity ? 0 : Math.round(minAltitude * 0.3048), // meters
    passes: passeDays,
  };
};

/**
 * Check if a day is a major pass day
 * @param {string|number} day - Day number
 * @returns {Object|null} Pass info if it's a pass day, null otherwise
 */
export const getPassInfo = (day) => {
  const passInfo = {
    8: { name: "Kongma La", altitude: "18,159 ft / 5,535 m", color: "#C22848" },
    14: { name: "Cho La", altitude: "17,782 ft / 5,420 m", color: "#C22848" },
    // Renjo La is on day 18 in this trek
    18: { name: "Renjo La", altitude: "17,585 ft / 5,360 m", color: "#C22848" },
  };
  
  const dayNum = parseInt(day);
  return passInfo[dayNum] || null;
};

/**
 * Check if a day is a summit day
 * @param {string|number} day - Day number
 * @returns {Object|null} Summit info if it's a summit day, null otherwise
 */
export const getSummitInfo = (day) => {
  const summitInfo = {
    7: { name: "Chhukung Ri", altitude: "18,209 ft / 5,550 m", color: "#C22848" },
    12: { name: "Kala Patthar", altitude: "18,514 ft / 5,643 m", color: "#C22848" },
    16: { name: "Gokyo Ri", altitude: "17,575 ft / 5,357 m", color: "#C22848" },
  };
  
  const dayNum = parseInt(day);
  return summitInfo[dayNum] || null;
};
